  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.product-container{
    border: 0.5px solid #666;
    padding: 10px;
    width: 200px;
    text-transform: uppercase;
    cursor: pointer;
    margin: 5px;
}

.image-container{
    overflow: hidden;
}
.product-image{
    width: 100%;
    height: 200px;
    object-fit: cover;
    -webkit-transition: all .8s ease-in-out;
    -moz-transition: all .8s ease-in-out;
    -o-transition: all .8s ease-in-out;
    -ms-transition: all .8s ease-in-out;
}
.product-image:hover{
    transform: scale(1.3);
}
.product-name{
    font-size: 20px;
    font-weight: 500;
}
.product-price{
    font-size: 15px;
}
.product-container:hover{
    box-shadow: 0px 0px 20px 9px rgba(0,153,48,0.27);
}
.product-gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 50px;
  }
.filter-button{
    background-color:#fff;
    height:80px;
    width:80px;
    border-radius: 50%;
    border: 0;
    outline:none;
    margin-bottom: 15px;
}
.filter-container{
    display: flex;
    flex-direction: column;
    width: 100px;
    margin: 20px;
    font-size: 20px;
    color: #618f49;
}
.filter-button-img{
    height: 45px;
    -webkit-filter: invert(62%) sepia(28%) saturate(675%) hue-rotate(55deg) brightness(101%) contrast(85%);
            filter: invert(62%) sepia(28%) saturate(675%) hue-rotate(55deg) brightness(101%) contrast(85%);
}
.filter-button:hover{
    box-shadow: 0px 0px 10px 9px rgba(0,153,48,0.1);
}
.filters-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}
.nuestros-productos{
    color:#618f49;
}
